body {
  margin: 0;
  padding: 0;

}

.donatePosition{
  position: relative;
}

.containerOfAll{
  display: flex;
  flex-direction: column;
  
}

.containerOfSentence{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-content:flex-start;

  margin-top: 50px;
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  height: 380px;
}

.partOfSentence{
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 10px;
  margin-left: 2px;
  margin-right: 2px;
  border: 1px solid black;
  width: auto;
  height: 52px;
  border-radius: 5px;
}

.btn{
 width: 150px;
 height: 50px;
 font-size: 16px;
 align-self:center;
}

.donateButton{
  bottom: 0%;
}